.footer{
    height:45vh;
    background-color: $tertiary;
    color: $primary;
    padding:8rem 0 5rem 0;

// Logo Column
    &__logo-box{
        height:8rem;
        text-align:center;
    }

    &__logo{
        height: 100%;
    }

    &__social-icon-list{
        list-style: none;
        width:70%;
        margin:0 auto;
        text-align: center;
    }

    &__social-icons{
        display:inline-block;
        padding: 1rem;

        & > a:link,
        & > a:visited{
            font-size: 2rem;
            color:$primary;
        }

        & > a:hover,
        & > a:active{
            color:$white-color;
        }


    }

    &__phone-box{
        padding:.5rem;
        text-align:center ;
    }

    &__phone-icon{
        transform: translateY(.3rem);
        margin:0 1rem;
    }

    &__phone{
        font-size:2rem;
        color:$primary;
        display: inline-block;
    }

    //Footer Nav column

    &__nav-list{
        list-style: none;
        padding: 1rem;
        text-align: center;

        @include design(tab-horizontal){
            padding-top: 9rem;
        }
    }

    &__nav-list-item{
        font-size: 2rem;
        font-weight:600;
        display: inline-block;
        
        &:not(:last-child){
            margin-right: 1.5rem;
        }

        & > a:link,
        & > a:visited{
            color:$primary;
        }

        & > a:hover,
        & > a:active{
            color:$white-color;
        }
        
    }

    //Footer author column

    &__author{
        font-size: 1.6rem;
        text-align: center;
        font-weight: 900;
        color:$primary;

        @include design(tab-horizontal){
            padding-top: 9rem;
        }

        & > a:link,
        & > a:visited{
            color:$primary;
        }
        
        & > a:hover,
        & > a:active{
            color: $white-color;
        }
    }
}