//For Mobile
.header{
    position: relative;
    top:-8.5rem;
    z-index: 1;
    width: 100%;
    height: 85vh;
    background-size: cover;
    background-position: center;

    &__home{
      background-image: linear-gradient(
        to bottom,
        rgba($secondary, 0.9),
        rgba($tertiary-dark-1, 0.3)
      ),
      url("../../images/toronto-landscape.jpg");
    }

    &__about{
      background-image: linear-gradient(
        to bottom,
        rgba($secondary, 0.9),
        rgba($tertiary-dark-1, 0.5)
      ),
      url("../../images/about-header.jpg");
    }

    &__why-study-abroad{
      background-image: linear-gradient(
        to bottom,
        rgba($secondary, 0.9),
        rgba($tertiary, 0.3)
      ),
      url("../../images/why-study-abroad-header.jpg");
      background-position: bottom;
    }

    &__heading-container{
      width:80%;
      position: absolute;
      top:35rem;
      left:2rem;
    }
  
    &__primary-heading{
      font-size: 4rem;
      font-weight: 300;
      color: $primary;
      display: block;
      letter-spacing: -.1rem;
      text-transform: uppercase;
    }
  
    &__btn{
      font-size:1.6rem;
    }
}